ul{
  list-style: none;
}

*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
/*body{
    min-width: 1180px;
    font-family: "Roboto", Sans-serif, Arial;
    color: #444444;
    font-size: 18px;
}*/
.container{
    width: 1140px;
    margin: 0 auto;
}
.container:after, .row:after, .row:before{
    content: '';
    clear: both;
    display: block;
}
.row{
    margin-left: -18px;
    margin-right: -18px;
}
*[class*="col"]{
    float: left;
    padding: 0 18px;
}

.col-1{width: 8.33%;}
.col-2{width: 16.66%;}
.col-3{width: 25%;}
.col-4{width: 33.33%;}
.col-5{width: 41.66%;}
.col-6{width: 50%;}
.col-7{width: 58.33%;}
.col-8{width: 66.66%;}
.col-9{width: 75%;}
.col-10{width: 83.11%;}
.col-11{width: 91.66%;}
.col-12{width: 100%;}
.col-20{width: 20%;}

.col-offset-1{margin-left: 8.33%;}
.col-offset-2{margin-left: 16.66%;}
.col-offset-3{margin-left: 25%;}
.col-offset-4{margin-left: 33.33%;}
.col-offset-5{margin-left: 41.66%;}
.col-offset-6{margin-left: 50%;}
.col-offset-7{margin-left: 58.33%;}
.col-offset-8{margin-left: 66.66%;}
.col-offset-9{margin-left: 75%;}
.col-offset-10{margin-left: 83.11%;}
.col-offset-11{margin-left: 91.66%;}
.col-offset-12{margin-left: 100%;}

img{
    max-width: 100%;
    max-height: 100%;
}

input, select, textarea, button{
    outline: none;
}
